export default () => {
  $('.result__list li').each(function () {
    this.showed = true;
  });

  //Анимация счета цифр
  const swingNumber = (params, number) => {
    $(params).each(function () {
      $(this)
        .prop('Counter', 0)
        .animate(
          {
            Counter: number,
          },
          {
            duration: 800,
            easing: 'swing',
            step: function (now) {
              $(this).text(
                Math.ceil(now)
                  .toString()
                  .replace(/\B(?=(?:\d{3})+(?!\d))/g, ' ')
              );
            },
          }
        );
    });
  };
  $(window).on('scroll resize load', function () {
    let scroll = $(window).scrollTop() + window.innerHeight - 10;
    
    $('.result__list li').each(function () {
      let number = $(this).find('b').data('number');
      let el = this;

      if (scroll > $(this).offset().top && this.showed) {
        swingNumber($(el).find('b'), number);
        let percent = 339.292 * (1 - number / 100) + 'px';
        $(this).find('svg .result__value').css('stroke-dashoffset', percent);
        this.showed = false;
      }
    });
  });
};
