import Swiper, { Pagination, Navigation } from 'swiper';
export default () => {
  Swiper.use([Pagination, Navigation]);
  let swiper = new Swiper('.personal .swiper-container', {
    slidesPerView: 1,
    speed: 400,
    spaceBetween: 10,
    centeredSlides: true,
    navigation: {
      prevEl: '.personal .swiper-button-prev',
      nextEl: '.personal .swiper-button-next',
    },
    // pagination: {
    //   el: '.slider .swiper-pagination',
    //   type: 'bullets',
    //   clickable: true,
    // },
    breakpoints: {
      768: {
        slidesPerView: 3,
        spaceBetween: 40,
      },
      1024: {
        slidesPerView: 3,
        spaceBetween: 60,
      },
    },
  });
  swiper.on('slideChangeTransitionEnd', function () {
    let dataName = $('.personal .swiper-slide-active').data('name');
    $(`.personal__list li[data-name=${dataName}]`).slideDown(200);
  });
  swiper.on('slideChangeTransitionStart', function () {
    $('.personal__list li').slideUp(200);
  });
};
