import Swiper, { Navigation } from 'swiper';
export default () => {
  Swiper.use([Navigation]);
  let swiper = new Swiper('.request .swiper-container', {
    slidesPerView: 1,
    speed: 400,
    spaceBetween: 10,
    centeredSlides: false,
    navigation: {
      prevEl: '.request .swiper-button-prev',
      nextEl: '.request .swiper-button-next',
    },
    breakpoints: {
      768: {
        slidesPerView: 2,
        spaceBetween: 40,
      },
      1024: {
        slidesPerView: 2,
        spaceBetween: 60,
      },
    },
  });
};
